.thum_img {
  position: relative;
  overflow: hidden;
  transition: all 0.7s ease-in-out;
  background-color: #4c4c4d;
}


.top-product-img {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}



.thum_img:hover .top-product-img {
  opacity: 1;
  transition: all 0.7s ease-in-out;
}


