/* TOP BTN */

.topBtn {
  position: fixed;
  opacity: 0;
  bottom: 50px;
  right: 50px;
  z-index: -10;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  border: 0 none;
  background-color: #ffffff;
  color: #333;
  box-shadow: 1px 1px 6px 3px rgba(170, 170, 170, 0.3);
  cursor: pointer;
  transition: opacity 0.3s ease-in;
}

.topBtn.active {
  z-index: 10;
  opacity: 1;
}

.topBtn:hover,
.topBtn:focus,
.topBtn:active {
  outline: 0 none;
}

/* SHOP 페이지 탭 메뉴 */

.nav-tabs {
  display: flex;
  align-items: flex-end;
}

.valnum {
  width: 24px;
  height: 24px;
  font-size: 10px;
  background-color: #ffffff;
  font-weight: bold;
  border: 1px solid #ddd;
  text-align: center;
  padding: 0;
}

.valnum:focus {
