.modalcustom {
  position: fixed;  
  top: 60%; 
  left: 50%;
  transform: translate(-50%, -50%);   
  opacity: 0.7;
  transition: 0.7s ease-in-out;  
  color: #333;
  font-size:15px;
  background-color: #fff;  
  border-radius: 5px;
  border: 1px solid #333;
  padding: 10px 30px;   
  z-index: 9999; 
}

.modal.hidden {
  opacity: 0;
}



